
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {getCookie} from '@/tool/common.js';
import Drive from '@/tool/classFactory/drive.js';
export default {
components: {
Group,
Cell
},
data () {
return {
msg: '请点击视频按钮观看,视频仅供参考！',
params:{
'subjectsType':'',
'sessionId':''
},
videoList:[]
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          

that.changeFixed(this.clientHeight)
},
created () {
this.params.subjectsType = this.$route.query.subjectsType || null
this.params.sessionId = this.getCookie('sessionId')
console.log(this.params)
this.getVideoList()
},
methods: {
getCookie: function (objName) {
var arrStr = document.cookie.split("; ")
for (var i = 0; i < arrStr.length; i++) {
var temp = arrStr[i].split("=")
if(temp[0] == objName)
return unescape(temp[1])
}
},
changeFixed(clientHeight) {                        //动态修改样式

this.$refs.homePage.style.height = clientHeight + 'px';

},
getVideoList () {
const that = this;
Drive.prototype.getVideoList(that.params).then(res => {
let list = res.data.data || [];
that.videoList = list
console.log(list)
});
},
onVideoTap (item){
console.log(item)
this.$router.push('../video?videoId=' + item.id)
}
}
}



